import React from "react";
import { observer, inject } from "mobx-react";
import { Modal, Toast } from "antd-mobile";

import "../style.scss";
import { title } from "process";
const RegOne = (props: any) => {
  let { registerState } = props;
  let {
    phoneNumber,
    register,
    changePhoneNum,
    changeStep,
  } = registerState as RegisterStore;
  const sendMsg = () => {
    register().then((res) => {
      if (res.code === 0) {
        Toast.success("", 1.5);
        changeStep(2);
      }
    });
  };
  const click = () => {
    if (phoneNumber.length !== 11) {
      Toast.fail("");
      return;
    }
    sendMsg();
  };
  return (
    <div className="main">
      <p className="reg-title"></p>
      <p className="reg-remark"></p>
      <p className="reg-mobile">
        <span className="area">+86</span>
        <input
          type="tel"
          pattern="\d*"
          autoFocus
          onChange={(e) => {
            changePhoneNum(e.target.value);
          }}
          placeholder="11"
        />
      </p>
      <p
        className={
          phoneNumber.length !== 11 ? "reg-btn no-activity" : "reg-btn"
        }
        onClick={click}
      >
        
      </p>
    </div>
  );
};
export default inject("registerState")(observer(RegOne));
